import * as React from 'react'
// import Collapsedmenu from './Collapsedmenu';

export default function Navmenu() {
  return (
    <div className='flex justify-between'>
      <div className='flex center-both'>
        <h1 className='top-title'>Travel Mate</h1>
      </div>
      <div className='flex'>
        <div className='hamburger-icon' onClick={()=>{
          if(!document.getElementsByClassName("nav-menu-container")[0].style.getPropertyValue("display")){
            document.getElementsByClassName("nav-menu-container")[0].style.display="block"
          }else if(document.getElementsByClassName("nav-menu-container")[0].style.getPropertyValue("display")==="block"){
            
            document.getElementsByClassName("nav-menu-container")[0].style.display="none"
          }else{
            document.getElementsByClassName("nav-menu-container")[0].style.display="block"

          }
        }}>
          <hr className='hamburger-line'/>
          <hr className='hamburger-line'/>
          <hr className='hamburger-line'/>
        </div>
        <ul className='flex nav-menu-container'>
          <li className='nav-li' onClick={()=>{
            console.log('clicked')
          }} >Home</li>
          <li className='nav-li'>Destinations</li>
          <li className='nav-li'>Hotels</li>
          <li className='nav-li'>Reviews</li>
          <li className='nav-li nav-booking'>Booking Form</li>
        </ul>
      </div>
    </div>
  );
}